:root {
  --primary-color: #5500FF;
  --secondary-color: #F97D91;
  --third-color: #4F4F4F;
  --footer-color: #130235;
}

body {
  font-family: "Cormorant Garamond", serif;
}

header.site-header .site-search .widget_product_search form:before {
  display: none;
}
header.site-header .site-search .widget_product_search form input[type=search] {
  background-color: transparent;
  box-shadow: none;
  border: 2px solid;
  padding: 1em !important;
  position: relative;
  right: 152px;
  top: 25px;
  width: 376px;
  border-radius: 6px;
  border-color: var(--primary-color);
  transition: all 0.15s ease-in-out;
  letter-spacing: 0.06em;
}
header.site-header .site-search .widget_product_search form input[type=search]:focus {
  outline: none;
  border-width: medium;
  box-shadow: 0px 1px 5px 1px #5500ff75;
}
@media screen and (max-width: 975px) {
  header.site-header .site-search .widget_product_search form input[type=search] {
    right: 195px;
  }
}
header.site-header .widget_product_search form button[type=submit] {
  clip-path: unset;
  clip: unset;
  overflow: visible;
  color: #000;
  right: 0;
  left: auto;
  background-color: transparent;
  height: 100%;
  width: 25%;
  top: 25px;
  visibility: hidden;
}
@media screen and (max-width: 1030px) {
  header.site-header .widget_product_search form button[type=submit] {
    right: -30px;
  }
}
@media screen and (max-width: 975px) {
  header.site-header .widget_product_search form button[type=submit] {
    right: 4px;
  }
}
header.site-header .site-branding img {
  width: 100px;
  height: 100px;
}

.site-header .custom-logo-link {
  outline: none;
}

.main-navigation ul li a,
ul.menu li a {
  color: var(--third-color);
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1em;
}

.site-header ul.menu li.current-menu-item > a {
  color: var(--primary-color);
  border-bottom: 2px solid #5500ff99;
}

.site-header-cart .cart-contents .count {
  opacity: 0.8;
  text-transform: initial;
}

.hentry .entry-header {
  display: none;
}

.page-template-template-homepage:not(.has-post-thumbnail) .site-main {
  padding-top: 0;
}

.page-template-template-homepage .type-page {
  padding-bottom: 0;
}

.hero {
  height: 435px;
  width: auto;
  background-color: rebeccapurple;
  position: relative;
  bottom: 68px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media screen and (max-width: 760px) {
  .hero {
    grid-template-columns: 1fr;
    margin-bottom: 15rem;
  }
}
.hero__category {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 -127px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 768px) {
  .hero__category {
    background-position: 0 -73px;
  }
}
@media screen and (max-width: 760px) {
  .hero__category {
    height: 350px;
  }
}
.hero__category-content {
  position: relative;
  z-index: 2;
  text-align: center;
}
.hero__category-title {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 700;
  color: #fff;
  font-size: 2.9em;
  padding: 4px 17px;
  border-radius: 4px;
  box-shadow: 0 0 2px 2px #0000000d;
  position: relative;
  top: 30px;
}
@media screen and (max-width: 765px) {
  .hero__category-title {
    font-size: 1.9em;
  }
}
.hero__category-link {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.7em;
  background: #fff;
  padding: 8px 24px;
  border-radius: 36px;
  box-shadow: 0 0 2px 2px #0000000d;
  position: relative;
  top: 74px;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width: 760px) {
  .hero__category-link {
    font-size: 1.3em;
  }
}
.hero__category-women {
  position: relative;
}
.hero__category-women h2 {
  background: #f97d91bd;
}
.hero__category-women a:hover {
  box-shadow: 0 0px 5px 2px #f97d91c4;
}
.hero__category-women:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #f97d91;
  background: linear-gradient(184deg, rgba(249, 125, 145, 0.7973564426) 0%, rgba(0, 0, 0, 0.3379726891) 100%);
}
.hero__category-men {
  position: relative;
}
.hero__category-men h2 {
  background: #5500ff82;
}
.hero__category-men a:hover {
  box-shadow: 0 0px 5px 4px #5500ff8c;
}
.hero__category-men:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #5500ff;
  background: linear-gradient(184deg, rgba(85, 0, 255, 0.800157563) 0%, rgba(0, 0, 0, 0.3379726891) 100%);
}

.content-area {
  margin-bottom: 0;
}

.page-template-template-homepage .type-page {
  padding: 0;
}

.storefront-product-section .section-title {
  font-size: 2.7em;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  font-weight: 700;
  color: var(--third-color);
}
@media screen and (max-width: 768px) {
  .storefront-product-section .section-title {
    font-size: 2.2em;
  }
}

ul.products li.product .woocommerce-LoopProduct-link {
  box-shadow: 0px 0px 3px 1px #00000026;
  text-align: left;
}
ul.products li.product .woocommerce-LoopProduct-link.women img {
  border: 1px solid #f97d9145;
}
ul.products li.product .woocommerce-LoopProduct-link.men img {
  border: 1px solid #5500ff2e;
}
ul.products li.product .woocommerce-LoopProduct-link h2, ul.products li.product .woocommerce-LoopProduct-link span.price, ul.products li.product .woocommerce-LoopProduct-link span.short_description {
  padding-left: 0.8em;
  display: block;
  padding-right: 0.8em;
}
ul.products li.product .woocommerce-LoopProduct-link h2 {
  font-weight: 700;
}
ul.products li.product .woocommerce-LoopProduct-link span.price {
  padding-bottom: 1.1em;
  font-size: 1.3em;
}
ul.products li.product .woocommerce-LoopProduct-link span.price ins span.woocommerce-Price-amount.amount {
  color: #ff005e;
  font-weight: 700;
}

ul.products li.product .button {
  box-shadow: 0px 0px 3px 1px #00000026;
}

.yith-wcwl-icon.fa-heart-o:before,
.yith-wcwl-icon.fa-heart:before {
  background: #ffffff26;
  padding: 6px;
  border-radius: 14px;
  box-shadow: 0px 0px 5px 1px #00000021;
  padding-top: 6px;
  padding-bottom: 4px;
  filter: grayscale(0);
  color: var(--secondary-color);
}

.yith-wcwl-icon.fa-heart-o:before {
  color: #0000004a;
}

.onsale {
  margin: 6px auto;
  display: inline-block;
  width: 100%;
  text-align: center;
  border: #fff 1px solid;
  background: #ff0063de;
  color: #fff;
  font-size: 1em;
}

.single-product .onsale {
  width: 50%;
}
@media screen and (max-width: 767px) {
  .single-product .onsale {
    width: 100%;
  }
}

.single-product .woocommerce-LoopProduct-link.woocommerce-loop-product__link .onsale {
  width: 100%;
}

.services {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  column-gap: 30px;
  border-top: 1px solid #5500ff78;
  border-bottom: 1px solid #5500ff78;
  padding-top: 30px;
  padding-bottom: 30px;
}
.services__content {
  text-align: center;
}
.services__content img {
  margin: 0 auto;
}
.services__content p {
  margin-top: 0.7em;
  border: 1px solid var(--secondary-color);
  padding: 0.6em;
  border-radius: 3px;
  font-weight: 600;
  line-height: 1.5em;
}

.owlCarouselz {
  height: 300px;
  box-shadow: 0px 0px 2px 2px #00000029;
  border-radius: 5px;
}
@media screen and (max-width: 600px) {
  .owlCarouselz {
    height: 400px;
  }
}
.owlCarouselz a {
  height: 200px;
  text-decoration: none !important;
}
.owlCarouselz__slide {
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
@media screen and (max-width: 600px) {
  .owlCarouselz__slide {
    background-position: bottom;
  }
}
.owlCarouselz__slide h3 {
  position: relative;
  z-index: 2;
  color: #fff;
  font-size: 1.9em;
  letter-spacing: 0.02em;
  font-weight: bold;
  text-transform: capitalize;
}
.owlCarouselz__slide-men:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #5500ffa3;
  border-radius: 5px;
}
.owlCarouselz__slide-women:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #f97d91e0;
  border-radius: 5px;
}

.owl-nav {
  position: absolute;
  top: 122px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
.owl-nav button.owl-prev:hover, .owl-nav button.owl-prev:focus,
.owl-nav button.owl-next:hover,
.owl-nav button.owl-next:focus {
  outline: none;
}
.owl-nav img {
  position: relative;
  pointer-events: all;
}
.owl-nav img:hover {
  outline: none !important;
}
.owl-nav img.left {
  right: 62px;
}
.owl-nav img.right {
  left: 62px;
}

.category-slider h2 {
  font-size: 2.1em;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  font-weight: 700;
  color: var(--third-color);
  text-align: center;
  margin-bottom: 1em;
}
@media screen and (max-width: 900px) {
  .category-slider h2 {
    font-size: 1.7em;
  }
}

.category-intro {
  height: 344px;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: 56px;
}
@media screen and (max-width: 768px) {
  .category-intro {
    bottom: 30px;
    background-position: center;
  }
}
.category-intro-men:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #5500ff;
  background: linear-gradient(181deg, rgba(85, 0, 255, 0.75) 0%, rgba(249, 125, 145, 0.43) 97%);
}
.category-intro-women:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #5500ff;
  background: linear-gradient(181deg, #f97d91e3 0%, rgba(75, 12, 193, 0.46) 97%);
}
.category-intro h1 {
  position: relative;
  z-index: 2;
  font-weight: bold;
  color: #fff;
  text-align: center;
  line-height: 54px;
  text-transform: uppercase;
  padding: 0 86px;
  text-shadow: 2px 2px 5px #0000008c;
  letter-spacing: 0.01em;
  font-size: 2.3em;
}
@media screen and (max-width: 900px) {
  .category-intro h1 {
    font-size: 1.5em;
    line-height: 34px;
  }
}
@media screen and (max-width: 600px) {
  .category-intro h1 {
    font-size: 1em;
    line-height: 24px;
    padding: 0;
  }
}

.single-product p {
  color: var(--third-color);
}

@media screen and (max-width: 600px) {
  .woocommerce-wishlist .product-name h3 {
    font-size: 1em;
  }
  .woocommerce-wishlist .product-name h3 a {
    font-weight: 600;
  }
}
.site-footer {
  background-color: var(--footer-color);
}
.site-footer img {
  margin: 0 auto;
  position: relative;
  top: 30px;
}

.footer-logo {
  position: relative;
  top: -48px;
}

.zoom-social-icons-list {
  text-align: center;
  position: relative;
  top: -60px;
}

#zoom-social-icons-widget-2 {
  margin-bottom: 0;
}

#custom_html-3 {
  margin: 0;
}

.footer-widgets {
  padding-top: 0.235801em;
}

.footer__payment {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(4rem, 1fr));
  justify-content: center;
  align-items: center;
  column-gap: 16px;
  position: relative;
  top: -32px;
}
.footer__payment img {
  width: 100%;
}

.site-footer .widget .widget-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.3em;
  font-weight: 600;
  letter-spacing: 0.06em;
  margin-bottom: 0.5em;
  padding-bottom: 0em;
  border-bottom: 1px solid #ffffffad;
}

.site-footer a:not(.button):not(.components-button) {
  font-weight: 100;
  color: #c3c3c3;
}
.site-footer a:not(.button):not(.components-button):hover {
  color: #fff;
}

.site-footer {
  padding-bottom: 0;
}

.footer__copyright {
  color: white;
  text-align: center;
  padding: 5px;
  background: #0c0123;
  font-size: 1.19em;
  font-weight: 100;
}
.footer__copyright a {
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  transition: all 0.2s ease-in-out;
}
.footer__copyright a:hover {
  color: var(--secondary-color);
}

.widget ul ul.children {
  display: none;
}